/* Affichage/Cacher menu des settings */
$('.burger').click(function () {
    // au click du bouton ayant la class burger :
    $(this).toggleClass('burger-open');
    // on ajoute ou enlve la class burger-open au bouton.burger
    $('.menu').toggleClass('is-open');
    // on ajoute ou enlve la class is-open  la nav.menu
});
$('.menu ul li a').click(function () {
    // au click d'un des a :
    $('.menu').removeClass('is-open');
    // on enlve la class is-open  la nav.menu
    $('.burger').removeClass('burger-open');
    // on enlve la class burger-open au bouton.burger
    // utile pour un one-page, car on ne veut pas que la nav reste ouverte une fois que la page est arrive au bon endroit
});

/* Close the settings menu if you click outside your DIVV */
let menu = document.querySelector('.menu')
let engrenage = document.querySelector(".fas");

window.addEventListener('click', function (e) {
    if (document.querySelector(".is-open") && e.target != menu && e.target != engrenage) {
        $('.menu').removeClass('is-open');
        $('.burger').removeClass('burger-open');
    }
})

